import React from "react";
import { TextComponent } from "../TextComponent";
import './ChangeKelas.scss';
const ChangeKelas = ({children, id}) => {
  return (
    <div className="pilih-kelas" id={id}>
     <div className="container">
     <TextComponent judul="Pilih Kategori Kelas">Yuk, pilih kategori kelas yang ingin kamu ikuti, semuanya gratis kok.Klik pada kategori yang ingin kamu ikuti untuk melihat kelas yang tersedia ya!.</TextComponent>
      <div className="card-wrapper">{children}</div>
     </div>
    </div>
  );
};
export default ChangeKelas;